import React from 'react';
import { Link } from 'react-router-dom';
import HomeIcon from '../../../assets/SvgComponents/HomeSvg/HomeIcon';
import HeaderLogo from '../../../assets/SvgComponents/SideBarSvg/SideBarLogoSvg';
import ChangeLanguageMenu from '../../../components/ChangeLanguageMenu/ChangeLanguageMenu';

const LoginHeader = () => (
  <header>
    <nav className="navbar navbar-default navbar-fixed-top bcp-top-header">
      <div className="container-fluid">
        <div className="navbar-header">
          <div className="c-header-icon-home">
            <Link to="/">
              <HomeIcon />
            </Link>
          </div>
          <Link className="navbar-brand" to="#">
            <HeaderLogo />
          </Link>
        </div>
        <ChangeLanguageMenu />
      </div>
    </nav>
  </header>
);

export default LoginHeader;
