//@flow
import React from 'react';
import { Formik } from 'formik';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import * as Yup from 'yup';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
import { ENROLLMENT_STEP3_NAMESPACE } from '../../Enrollment/constants';
const RESET_PASSWORD_NAMESPACE = 'Login:resetPassword.';
const validationSchema = Yup.object().shape({
  codeOtp: Yup.string().required(
    t(`${RESET_PASSWORD_NAMESPACE}otpRequiredMsg`),
  ),
  newPassword: Yup.string()
    .required(t(`${ENROLLMENT_STEP3_NAMESPACE}passwordRequiredMsg`))
    .min(8, t(`${ENROLLMENT_STEP3_NAMESPACE}passwordMinLengthMsg`))
    .max(12, t(`${ENROLLMENT_STEP3_NAMESPACE}passwordMaxLengthMsg`))
    .matches(
      /^(?=.*[a-z]).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordLowerCase`),
    )
    .matches(
      /^(?=.*[A-Z]).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordUpperCase`),
    )
    .matches(
      /^(?=.*\d).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordNumber`),
    )
    .matches(
      /^(?=.*[@$!%*?&]).*$/,
      t(`${ENROLLMENT_STEP3_NAMESPACE}invalidPasswordSpecialCharacter`),
    ),
  passwordConfirm: Yup.string()
    .required(t(`${RESET_PASSWORD_NAMESPACE}confirmationPasswordRequiredMsg`))
    .oneOf(
      [Yup.ref('newPassword'), null],
      t(`${RESET_PASSWORD_NAMESPACE}passwordShouldMatchMsg`),
    ),
});
const initialValues = {
  codeOtp: '',
  newPassword: '',
  passwordConfirm: '',
};
type Props = {
  active: boolean,
  submitStepRequest: Function,
  handlePrecedent: Function,
  setFormRef: Function,
};
class ResetPasswordStep2 extends React.PureComponent<Props> {
  state = { showPasswords: false };
  _showPasswords = () => {
    this.setState({
      showPasswords: true,
    });
  };
  _hidePasswords = () => {
    this.setState({ showPasswords: false });
  };
  componentDidMount() {
    document.addEventListener('mouseup', this._hidePasswords);
  }
  componentWillUnmount() {
    document.removeEventListener('mouseup', this._hidePasswords);
  }
  render() {
    let className = this.props.active ? 'step-pane active' : 'step-pane';
    return (
      <div className={className} data-step="1">
        <Formik
          ref={ref => this.props.setFormRef(ref)}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            this.props.submitStepRequest(values);
          }}
          initialValues={initialValues}
          enableReinitialize
        >
          {formikProps => {
            const {
              values,
              handleChange,
              handleBlur,
              handleSubmit,
              isValid,
            } = formikProps;
            return (
              <form onSubmit={handleSubmit}>
                <div className="form-group">
                  <input
                    className="form-control"
                    name="codeOtp"
                    type="text"
                    autoComplete="new-password"
                    value={values.codeOtp}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    placeholder={t(`${RESET_PASSWORD_NAMESPACE}otpPlaceholder`)}
                  />
                  <ExpiringFormFieldError name="codeOtp" />
                </div>
                <div className="form-group">
                  <input
                    className="form-control"
                    name="newPassword"
                    type={this.state.showPasswords ? 'text' : 'password'}
                    value={values.newPassword}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    placeholder={t(
                      `${RESET_PASSWORD_NAMESPACE}newPasswordPlaceholder`,
                    )}
                  />
                  <span className="input-show-btn">
                    <button
                      className="btn reveal"
                      type="button"
                      onMouseDown={this._showPasswords}
                    />
                  </span>
                  <ExpiringFormFieldError name="newPassword" />
                </div>
                <div className="form-group">
                  <input
                    className="form-control"
                    name="passwordConfirm"
                    type={this.state.showPasswords ? 'text' : 'password'}
                    value={values.passwordConfirm}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    placeholder={t(
                      `${RESET_PASSWORD_NAMESPACE}newPasswordConfirmPlaceholder`,
                    )}
                  />
                  <ExpiringFormFieldError name="passwordConfirm" />
                </div>

                <div className="form-group">
                  <div className="align-right text-right">
                    <button
                      className="btn btn-default btn-space wizard-previous"
                      data-wizard="#wizard1"
                      onClick={this.props.handlePrecedent}
                    >
                      {t(`${RESET_PASSWORD_NAMESPACE}precedentBtn`)}
                    </button>
                    <button
                      className="btn btn-secondary btn-space wizard-next"
                      data-wizard="#wizard1"
                      onClick={handleSubmit}
                      disabled={!isValid}
                    >
                      {t(`${RESET_PASSWORD_NAMESPACE}validateBtn`)}
                    </button>
                  </div>
                </div>
              </form>
            );
          }}
        </Formik>
      </div>
    );
  }
}
export default withTranslation()(ResetPasswordStep2);
